<template>
  <v-row justify="center" no-gutters>
    <v-col cols="12" md="10" lg="8">
      <v-toolbar flat>
        <v-toolbar-title>Organization manage</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon small @click="fetchIndex(true)" class="mr-2">
          <v-icon>mdi-refresh</v-icon>
        </v-btn>
        <v-btn icon small @click="showCreate">
          <v-icon>mdi-plus</v-icon>
        </v-btn>
      </v-toolbar>
      <v-card flat v-if="loading">
        <v-row>
          <v-col cols="6" md="3">
            <v-skeleton-loader type="list-item-two-line"></v-skeleton-loader>
          </v-col>
          <v-col cols="6" md="3">
            <v-skeleton-loader type="list-item-two-line"></v-skeleton-loader>
          </v-col>
          <v-col cols="6" md="3">
            <v-skeleton-loader type="list-item-two-line"></v-skeleton-loader>
          </v-col>
          <v-col cols="6" md="3">
            <v-skeleton-loader type="list-item-two-line"></v-skeleton-loader>
          </v-col>
          <v-col cols="6" md="3">
            <v-skeleton-loader type="list-item-two-line"></v-skeleton-loader>
          </v-col>
          <v-col cols="6" md="3">
            <v-skeleton-loader type="list-item-two-line"></v-skeleton-loader>
          </v-col>
          <v-col cols="6" md="3">
            <v-skeleton-loader type="list-item-two-line"></v-skeleton-loader>
          </v-col>
          <v-col cols="6" md="3">
            <v-skeleton-loader type="list-item-two-line"></v-skeleton-loader>
          </v-col>
        </v-row>
      </v-card>
      <template v-else-if="data.length > 0">
        <v-row>
          <v-col
            cols="12"
            md="3"
            lg="3"
            v-for="(item, index) in data"
            :key="`organization-${item.id}`"
          >
            <v-card>
              <v-card-title class="text-center">
                <span>{{ item.name }}</span>
                <v-img
                  :src="`https://beeznests-cdn.fra1.cdn.digitaloceanspaces.com/img/badge_${item.badge}.png`"
                  max-height="18"
                  max-width="18"
                  contain
                  class="align-self-center mr-1"
                ></v-img>
                <v-spacer> </v-spacer>
                <v-btn icon small @click="edit(index)">
                  <v-icon small>mdi-pencil</v-icon>
                </v-btn>
                <v-btn
                  icon
                  small
                  @click="deleteData(index)"
                  :loading="item.deleteLoading"
                  :disabled="item.user_count > 0"
                >
                  <v-icon small>mdi-close</v-icon>
                </v-btn>
              </v-card-title>
              <v-list dense nav>
                <v-list-item>
                  <v-list-item-title>{{ item.show_text }}</v-list-item-title>
                </v-list-item>
                <v-list-item>
                  <v-list-item-title>{{ item.code }}</v-list-item-title>
                  <v-list-item-action
                    ><v-btn icon small @click="copyCode(item.id)">
                      <v-icon small>mdi-content-copy</v-icon>
                    </v-btn></v-list-item-action
                  >
                </v-list-item>
                <v-list-item>
                  <v-list-item-title class="grey--text"
                    >Students({{ item.user_count }})</v-list-item-title
                  >
                  <input
                    :id="`copy-txt-${item.id}`"
                    :value="item.code"
                    style="opacity: 0; width: 1px;"
                    type="text"
                  />
                </v-list-item>
                <v-list-item>
                  <v-list-item-title>isFree</v-list-item-title>
                  <v-list-item-action>
                    <v-switch
                      v-model="item.type"
                      true-value="free"
                      :false-value="null"
                      disabled
                    ></v-switch>
                  </v-list-item-action>
                </v-list-item>
              </v-list>
            </v-card>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12">
            <v-pagination
              v-model="meta.current_page"
              :length="meta.last_page"
              :total-visible="7"
            ></v-pagination>
          </v-col>
        </v-row>
      </template>
    </v-col>
    <v-dialog
      class="elevation-0"
      v-model="createDialog"
      max-width="600px"
      :overlay-opacity="0.1"
      persistent
    >
      <v-card>
        <v-card-title>
          <span class="headline">Add Organization</span>
          <v-spacer> </v-spacer>
          <v-btn icon @click.stop="createDialog = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-card-title>
        <v-card-text>
          <v-text-field
            label="Organization Name"
            v-model="activeData.name"
          ></v-text-field>
          <v-text-field
            label="Organization Show Text"
            v-model="activeData.show_text"
          ></v-text-field>
          <v-text-field
            label="Organization Code"
            v-model="activeData.code"
          ></v-text-field>
          <v-text-field
            label="Description"
            v-model="activeData.description"
          ></v-text-field>
          <v-list-item-title>Badge</v-list-item-title>
          <v-radio-group v-model="activeData.badge" row dense>
            <v-radio
              :label="i"
              :color="i"
              :value="i"
              v-for="i in badges"
              :key="`badge-${i}`"
            ></v-radio>
          </v-radio-group>

          <v-list-item>
            <v-list-item-title>isFree</v-list-item-title>
            <v-list-item-action>
              <v-switch
                v-model="activeData.type"
                true-value="free"
                :false-value="null"
              ></v-switch>
            </v-list-item-action>
          </v-list-item>
        </v-card-text>
        <v-card-actions>
          <v-btn
            color="orange darken-1"
            text
            :loading="saveLoading"
            @click="
              activeData.id <= 0 ? createOrganization() : updateOrganization()
            "
            >Save</v-btn
          >
          <v-btn color="grey darken-1" text @click.stop="createDialog = false"
            >Cancel</v-btn
          >
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>
<script>
import { show, create, update, destroy, fetch } from '@/api/organizcation'
export default {
  data: () => ({
    createDialog: false,
    saveLoading: false,
    loading: false,
    activeData: {
      name: undefined,
      code: undefined,
      id: 0,
    },
    data: [],
    meta: {
      current_page: 1,
      last_page: 1,
    },
    badges: ['green', 'purple', 'blue', 'yellow'],
  }),
  mounted() {
    this.fetchIndex()
  },
  watch: {
    'meta.current_page'() {
      this.fetchIndex()
    },
  },
  methods: {
    showCreate() {
      this.activeData = {
        name: undefined,
        code: undefined,
        id: 0,
      }
      this.showCreateDialog()
    },
    showCreateDialog() {
      this.createDialog = true
    },
    copyCode(id) {
      try {
        const copyDom = document.getElementById(`copy-txt-${id}`)
        copyDom.select()
        const successful = document.execCommand('copy')
        const msg = successful ? 'successful' : 'unsuccessful'
        this.$message('The code is copied!')
      } catch (err) {
        this.$message(
          "Your browser doesn't support it. Please copy it manually",
          'error'
        )
      }
    },
    edit(index) {
      this.activeData = Object.assign({}, this.data[index])
      this.editIndex = index
      this.showCreateDialog()
    },
    async deleteData(index) {
      this.$set(this.data[index], 'deleteLoading', true)
      try {
        await destroy(this.data[index].id)
        this.data.splice(index, 1)
      } catch (e) {
        this.$set(this.data[index], 'deleteLoading', false)
      }
    },
    async updateOrganization() {
      this.saveLoading = true
      try {
        const postData = Object.assign({}, this.activeData)
        const { data } = await update(this.activeData.id, postData)
        const index = this.editIndex
        this.$set(this.data[index], 'name', data.name)
        this.$set(this.data[index], 'code', data.code)
        this.$set(this.data[index], 'type', data.type)
        this.$set(this.data[index], 'badge', data.badge)
        this.createDialog = false
        this.saveLoading = false
      } catch (e) {
        this.saveLoading = false
      }
    },
    async createOrganization() {
      this.saveLoading = true
      try {
        const postData = Object.assign({}, this.activeData)
        const { data } = await create(postData)
        if (data.id > 0) {
          this.data.push(data)
        }
        this.createDialog = false
        this.saveLoading = false
      } catch (e) {
        this.saveLoading = false
      }
    },
    async fetchIndex(reset = false) {
      this.loading = true
      const params = {
        page: this.meta.current_page,
      }
      if (reset) {
        this.data = []
        params.page = 1
      }
      try {
        const { data, meta } = await fetch(params)
        console.log(data, meta)
        this.data = data
        this.meta = meta
        this.loading = false
      } catch (e) {
        this.loading = false
      }
    },
  },
}
</script>
