              </div>
              <div
                class="tw-absolute tw-z-50 tw-rounded-md tw-shadow-lg"
                :style="`display: ${showArray[index] ? 'block' : 'none'}; ${
                  index <= 4
                    ? 'left: -10px;'
                    : index === 5
                    ? 'transform: translateX(-50%);'
                    : 'transform: translateX(-70%);'
                }`"
                style="z-index: 99;"
                v-if="item.children.length > 0"
                :ref="`dropdown-${index}`"
              >
