<script>
import { mapGetters, mapState } from 'vuex'
export default {
  computed: {
    ...mapGetters(['isLoggedIn', 'userInfo']),
    ...mapState({
      categories: (state) =>
        state.category.data.filter((i) => i.parent_id === 0),
    }),
    userMenus() {
      const user_id = this.isLoggedIn ? this.userInfo.id : 0
      return [
        {
          name: 'My Profile',
          route: {
            name: 'NewProfile',
          },
        },
        {
          name: 'Edit Profile',
          route: {
            name: 'userSettings',
          },
        },
        {
          name: 'Add Portfolio',
          route: {
            name: 'portfolioCreate',
          },
        },
        {
          name: 'Posted Projects',
          route: {
            name: 'userProjects',
            params: {
              id: user_id,
            },
          },
        },
        // {
        //   name: 'Posted Discussions',
        //   route: {
        //     name: 'userChallenges',
        //     params: { id: user_id },
        //   },
        // },
        {
          name: 'Received Projects',
          route: {
            name: 'userReceives',
            params: { id: user_id },
          },
        },
        // {
        //   name: 'My Replies',
        //   route: {
        //     name: 'userReplies',
        //     params: { id: user_id },
        //   },
        // },
        {
          name: 'Performance Review',
          route: {
            name: 'userReviews',
            params: { id: user_id },
          },
        },
        {
          name: 'Organization Manage',
          route: {
            name: 'organizationIndex',
          },
          isAdmin: true,
        },
        {
          name: 'Links Manage',
          route: {
            name: 'linkIndex',
          },
          isAdmin: true,
        },
        {
          name: 'Promotions Manage',
          route: {
            name: 'promotionIndex',
          },
          isAdmin: true,
        },
        {
          name: 'Change Password',
          route: {
            name: 'userPassword',
          },
        },
        {
          name: 'Logout',
        },
      ]
    },
  },
  data: () => ({
    showCategory: false,
    showArray: [],
    navHidden: true,
  }),
  mounted() {
    if (this.categories.length <= 0) {
      this.$store.dispatch('category/getCategories').then(() => {
        this.setCategories()
      })
    } else {
      this.setCategories()
    }
  },
  methods: {
    logout() {
      this.$store.dispatch('auth/logout').then(() => {
        location.reload()
      })
    },
    setCategories() {
      this.categories.forEach((i, index) => {
        this.$set(this.showArray, index, false)
      })
    },
    setShow(index) {
      this.$set(this.showArray, index, true)
    },
    setHide(index) {
      this.$set(this.showArray, index, false)
    },
  },
}
</script>
