<template>
  <span>{{ content }}</span>
</template>
<script>
export default {
  props: {
    //   mentor
    isProject: {
      type: Boolean,
      default: true,
    },
    // 
    provideSolution: {
      type: Boolean,
      default: false,
    },
    // 
    type: {
      type: String,
      default: 'task',
    },
  },
  computed: {
    content() {
      return this.isProject
        ? this.type === 'academic-team'
          ? 'Skills and experience the team lead (industry expert) possess:'
          : 'Skills and experience you possess:'
        : 'Hands-on Project for Students:'
    },
  },
}
</script>
