<script>
import { mapGetters } from 'vuex'
import MobileHeader from '@/views/new-pages/common/MobileHeader'
import MobileDrawer from '@/views/new-pages/common/MobileDrawer'

export default {
  components: { MobileDrawer, MobileHeader },
  computed: {
    ...mapGetters(['authBackTo', 'loginLoading', 'authRedirectTo', 'isMobile']),
  },
  mounted() {
    document.documentElement.style.fontSize = '14px'
  },
  destroyed() {
    // document.documentElement.style.fontSize = '16px'
  },
  data: () => ({
    emailFocusIn: false,
    passwordFocusIn: false,
    email: null,
    password: null,
    hasError: false,
    errorMessage: null,
  }),
  methods: {
    emailFocus() {
      this.emailFocusIn = true
    },
    passwordFocus() {
      this.passwordFocusIn = true
    },
    async loginSubmit() {
      //   store 
      try {
        const loginData = {
          email: this.email,
          password: this.password,
          remember: this.remember,
        }
        if (await this.$store.dispatch('auth/login', loginData)) {
          this.$router.push(this.authRedirectTo)
        }
      } catch (e) {
        if (e.response && e.response.data && e.response.data.message) {
          this.hasError = true
          this.errorMessage = e.response.data.message
        }
      }
    },
  },
}
</script>
