// import store from '@/store'
export default {
  path: '/challenges',
  component: () => import('@/views/layouts/root'),
  children: [
    {
      path: '',
      name: 'challengeIndex',
      component: () => import('@/views/challenges/index'),
      meta: {
        keepAlive: true,
      },
    },
    {
      path: 'create',
      name: 'challengeCreate',
      component: () => import('@/views/challenges/create'),
      meta: {
        auth: true,
        settings: true,
      },
    },
    {
      path: ':id/edit',
      name: 'challengeEdit',
      component: () => import('@/views/challenges/create'),
      meta: {
        auth: true,
        settings: true,
      },
    },
    {
      path: ':id',
      name: 'challengeShow',
      component: () => import('@/views/challenges/show'),
    },
  ],
}
