<script>
import { mapGetters } from 'vuex'
export default {
  props: ['receiver', 'project'],
  computed: {
    ...mapGetters(['userInfo', 'isLoggedIn']),
    isCreate() {
      return (
        this.isLoggedIn &&
        (this.receiver.user.id === this.userInfo.id || this.userInfo.id <= 2)
      )
    },
    isProjectPublisher() {
      return (
        this.isLoggedIn &&
        (this.project.user.id === this.userInfo.id || this.userInfo.id <= 2)
      )
    },
  },
  methods: {
    review() {
      this.$emit('review')
    },
    /**
     * 
     */
    checkout() {
      this.$emit('checkout')
    },
    complete() {
      this.$emit('complete')
    },
    finished() {
      this.$emit('finished')
    },
    locationTo(href) {
      window.location.href = href
    },
  },
}
</script>
