          <div class="tw-mt-12 tw-flex tw-flex-wrap tw-justify-between tw-mx-4">
            <div
              class="tw-p-5 tw-rounded-md tw-mb-6 tw-border"
              style="
                max-width: 100%;
                width: 100%;
                border-color: #ddd;
                box-shadow: 0 3px 20px 0 rgba(0, 0, 0, 0.07);
              "
              v-for="(item, i) in data"
