                  >
                    <img
                      :src="item.user.avatar"
                      class="tw-w-20 tw-h-20 tw-rounded"
                      style="min-width: 70px;"
                  /></router-link>
                </div>
                <div
                  class="tw-ml-3"
                  :class="
                    item.type === 'mentor-program'
                      ? 'tw-flex tw-flex-col tw-justify-between'
                      : ''
                  "
                >
                  <div class="tw-flex tw-flex-row tw-items-center">
