                    <template v-if="item.type === 'task'">
                      <template v-if="item.cost_min <= 0">Free</template
                      ><template v-else>{{ item.cost_min }}</template>
                    </template>
                    <template v-else>
                      <template v-if="!item.reward">Free</template
                      ><template v-else>{{ item.reward }}</template>
                    </template>
