        </v-main>
      </v-app>
    </template>
  </div>
</template>
<script>
import CommonHeader from './common/header'
import CommonFooter from './common/footer'
import { fetchIndex } from '@/api/project'
import MobileHeader from '@/views/new-pages/common/MobileHeader'
import { mapGetters } from 'vuex'
import MobileFooter from '@/views/new-pages/common/MobileFooter'
import MobileDrawer from '@/views/new-pages/common/MobileDrawer'
import UserBadge from '@/components/User/Badge'
import ProjectType from '@/components/Project/Type'
import AcademicTeamLabel from '@/components/Project/AcademicTeamLabel'
export default {
  components: {
    ProjectType,
    MobileDrawer,
    MobileFooter,
    CommonFooter,
    CommonHeader,
    MobileHeader,
    UserBadge,
    AcademicTeamLabel,
  },
  computed: {
    ...mapGetters(['isMobile']),
    type() {
      return this.$route.name === 'mentorIndex' ? 'mentor-program' : 'task'
    },
    isProject() {
      return this.$route.name === 'NewProjects'
    },
  },
  mounted() {
    document.documentElement.style.fontSize = '14px'
    this.loadData(true)
  },
  destroyed() {
    // document.documentElement.style.fontSize = '16px'
  },
  watch: {
    '$route.query.category_id'(category_id) {
      this.category_id = '' + category_id
      this.loadData(true)
    },
    '$route.name'() {
      this.loadData(true)
    },
    filterType() {
      this.loadData(true)
    },
  },
  data: () => ({
    data: [],
    page: 1,
    last_page: 1,
    noMoreData: false,
    loading: false,
    categories: [],
    category_id: 0,
    keyword: '',
    filterType: 'task',
  }),
  methods: {
    async loadMore() {
      if (this.noMoreData) {
        return
      }
      this.page++
      await this.loadData()
    },
    async loadData(reset = false) {
      if (reset) {
        this.page = 1
        this.data = []
        this.noMoreData = false
      }
      this.loading = true
      const requestData = {
        includes: 'summary',
        page: this.page,
        type: this.type,
      }
      this.keyword ? (requestData.position = this.keyword) : null
      this.category_id > 0 ? (requestData.category_id = this.category_id) : null
      if (this.type === 'task') {
        requestData.type = this.filterType
      }
      const res = await fetchIndex(requestData)
      this.data = reset ? res.data : this.data.concat(res.data)
      this.noMoreData = res.meta.last_page === this.page
      this.loading = false
      if (reset) {
        this.categories = res.categories
      }
    },
  },
}
</script>
