<template>
  <v-col cols="12">
    <v-row justify="center">
      <v-col cols="12" md="8">
        <v-row>
          <v-col cols="12" xl="8">
            <template v-if="data.length > 0">
              <transition-group
                name="list-complete"
                tag="div"
                style="position: relative;"
              >
                <item
                  v-for="(item, index) in data"
                  :data="item"
                  :key="`${item.id}`"
                  :class="{ 'mt-5': index > 0 }"
                  class="list-complete-item"
                  @followed="followed"
                  @unfollow="unfollow"
                ></item>
              </transition-group>
            </template>
            <v-alert type="error" v-if="!loading && loadError" class="mt-5">
              Load data error.
            </v-alert>
            <v-alert type="info" v-if="!loading && noData" class="mt-5">
              Nothing found here.
            </v-alert>
            <v-skeleton-loader
              ref="skeleton"
              type="article, article, article, article, article"
              class="mx-auto mt-5"
              v-if="loading"
            ></v-skeleton-loader>
            <v-sheet v-intersect="onIntersect" v-if="!noMoreData"></v-sheet>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
  </v-col>
</template>
<script>
import { fetchFollowings } from '@/api/user'
import Item from '@/components/Follow/Item'
import _ from 'lodash'
export default {
  components: {
    Item,
  },
  computed: {
    id() {
      return this.$route.params.id || 0
    },
    noData() {
      return this.data.length <= 0
    },
  },
  data: () => ({
    loading: false,
    loadError: false,
    pages: {
      current_page: 1,
      last_page: 1,
      per_page: 5,
    },
    data: [],
    noMoreData: false,
  }),
  created() {
    this.loadData(true)
  },
  methods: {
    async loadData(reset = false) {
      try {
        this.loading = true
        const requestData = {}
        requestData.page = ++this.pages.current_page
        requestData.per_page = this.pages.per_page
        if (reset) {
          requestData.page = 1
        }
        const { data, meta, followed } = await fetchFollowings(
          this.id,
          requestData
        )
        this.data = reset ? data : this.data.concat(data)
        this.pages = meta
        if (requestData.page === meta.last_page) this.noMoreData = true
        if (followed) {
          this.$store.dispatch('follow/update', followed)
        }
      } catch (e) {
        this.loadError = true
      } finally {
        this.loading = false
      }
    },
    onIntersect(entries) {
      if (entries[0].isIntersecting && !this.noMoreData && !this.loading) {
        this.loadData()
      }
    },
    followed(id) {},
    unfollow(id) {
      const index = this.data.findIndex((item) => item.id === id)
      this.data.splice(index, 1)
      //_.remove(this.data, (item, i) => i === index)
    },
  },
}
</script>
<style scoped lang="scss">
.list-complete-item {
  transition: all 1s;
}
.list-complete-enter,
.list-complete-leave-to {
  transform: scale(0, 0);
}
.list-complete-leave-active {
  position: absolute;
}
</style>
