<template>
  <span>{{ content }}</span>
</template>

<script>
export default {
  props: {
    //   mentor
    isProject: {
      type: Boolean,
      default: true,
    },
    // 
    provideSolution: {
      type: Boolean,
      default: false,
    },
    // 
    type: {
      type: String,
      default: 'task',
    },
  },
  computed: {
    content() {
      return this.isProject && this.type === 'academic-team'
        ? this.provideSolution
          ? 'Project Description / The problem you are facing'
          : 'Project Description'
        : this.isProject
        ? 'Project/InternDescription:'
        : 'What the mentor can offer'
    },
  },
}
</script>
