export default {
  path: '/blog',
  component: () => import('@/views/layouts/root'),
  children: [
    {
      path: 'links',
      name: 'blogLinks',
      component: () => import('@/views/blog/links'),
      meta: {
        auth: true,
      },
    },
    {
      path: '',
      name: 'blogIndex',
      component: () => import('@/views/blog/root'),
      redirect: {
        name: 'articleShow',
        params: {
          id: 1,
        },
      },
      children: [
        {
          path: ':id',
          component: () => import('@/views/blog/show'),
          name: 'articleShow',
        },
      ],
    },
  ],
}
