<template>
  <span>{{ content }}</span>
</template>
<script>
export default {
  props: {
    //   mentor
    isProject: {
      type: Boolean,
      default: true,
    },
    // 
    provideSolution: {
      type: Boolean,
      default: false,
    },
    // 
    type: {
      type: String,
      default: 'task',
    },
  },
  computed: {
    content() {
      return this.isProject && this.type === 'academic-team'
        ? 'What the academic team will do'
        : this.isProject
        ? 'What you will do'
        : 'Learning objectives for the course'
    },
  },
}
</script>
